import React from 'react';

type PointCoords = [number, number];

interface Props {
  className?: string;
  color: string;
  width: number;
  height: number;
  points: [PointCoords, PointCoords, PointCoords];
}

/**
 * @description points: [x, y] ìˆœì„œë¡œ ëœ íŠœí”Œì„. x, yëŠ” ë¹„ìœ¨ì„ ì˜ë¯¸í•œë‹¤. ex) [0.5, 0.7]
 */
const TriangleSvg = ({ width, height, points, color, className }: Props) => {
  const path = points.map(([x, y]) => `${width * x}, ${height * y}`).join(' ');

  return (
    <svg width={width} height={height} className={className}>
      <polygon points={path} fill={color} />
    </svg>
  );
};

export default TriangleSvg;
