import React, { useMemo } from 'react';
import { getArc } from 'src/utils/arc';

interface Props {
  className?: string;
  color: string;
  size: number;
  degree: number;
}
const ArcSvg = ({ size, color, degree, className }: Props) => {
  const arcData = useMemo(
    () =>
      getArc({
        x: size / 2,
        y: size / 2,
        radius: size / 2,
        degree,
      }),
    [size, degree]
  );

  return (
    <svg width={size} height={size} className={className}>
      <path d={arcData} fill={color} />
    </svg>
  );
};

export default ArcSvg;
