import React from 'react';

interface Props {
  className?: string;
  color: string;
  size: number;
}
const CircleSvg = ({ size, color, className }: Props) => {
  return (
    <svg width={size} height={size} className={className}>
      <circle cx={size / 2} cy={size / 2} r={size / 2} fill={color}></circle>
    </svg>
  );
};

export default CircleSvg;
